:root {
  --dark: #000;
  --white: #F9EDED;
  --gatter: 50px;
}

.wrapper {
  min-height: 100vh;
  height: 100%;
  overflow-y: auto;
}
.main {
  margin: var(--gatter);
  // min-height: 100vh;
  // padding: var(--gatter);
}

.hero {
  // min-height: 100vh;
  position: relative;
  height: 100%;
  border: 1px solid var(--white);
  display: flex;
  align-items: center;
  justify-content: space-between;
  overflow: hidden;
}

.descr-left {
  max-width: 460px;
  width: 100%;
  text-align: center;
  margin-left: 60px;
  &__subtitle {
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 11px;
    @extend %titiweb-400;
  }
  &__text {
    position: relative;
    @extend %titiweb-800;
    font-size: 70px;
    line-height: 70px;
    letter-spacing: -7px;

    text-shadow: 0.05em 0 0 #00fffc, -0.03em -0.04em 0 #fc00ff,
    0.025em 0.04em 0 #fffc00;

    animation: glitch 1s infinite;

    &::before,
    &::after {
      position: absolute;
      content: 'halloween';
      top: 0;
      left: 0;
      left: 36px;
      letter-spacing: 1px;
      font-weight: 700;
      will-change: transform;
    }

    &::before {
      animation: glitch 0.75s infinite;
      clip-path: polygon(0 0, 100% 0, 100% 40%, 0 40%);
      transform: translate(-0.04em, -0.03em);
      transform: translate(-6px,-4px);
      opacity: 0.75;
    }

    &::after {
      animation: glitch 0.5s infinite;
      clip-path: polygon(0 60%, 100% 60%, 100% 100%, 0 100%);
      transform: translate(1px, 5px);
      opacity: 0.75;
    }
  }

  &__party {
    font-size: 18px;
    line-height: 28px;
    letter-spacing: 50px;
    padding-left: 60px;
    @extend %titiweb-400;
  }
}

.latter-800 {
    @extend %titiweb-800;
}
.latter-700 {
    @extend %titiweb-700;
}
.latter-600 {
    @extend %titiweb-600;
}
.latter-400 {
    @extend %titiweb-400;
}

.descr-right {
    max-width: 600px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-right: 20px;
    &__dj {
    @extend %titiweb-400;
    font-size: 18px;
    line-height: 20px;
    text-align: center;
    max-width: 250px;
    margin-bottom: 30px;
  }
  &__date {
    @extend %titiweb-700;
    font-size: 65px;
    margin-bottom: 30px;
    sup {
      top: -25px;
      font-size: 30px;
    }
    span {
    @extend %titiweb-400;
    }
  }
  &__text {
    @extend %titiweb-400;
    font-size: 18px;
    line-height: 20px;
    text-align: center;
    margin-bottom: 40px;
  }
}
.my-site {
  @extend %titiweb-700;
  position: relative;
  font-size: 40px;

  &::before,
  &::after {
    position: absolute;
    content:'sfizer.ru';
    top: 0;
    left: 0;
    right: 0;
    overflow: hidden;
    color: var(--white);
    background-color:var(--dark);
  }

  &:hover {
    &::before {
      animation: glitch-site  4s  linear;
    }
    &::after {
      animation: glitch-site  3s  linear;
    }
  }
}



.music {
  position: absolute;
  width: 50px;
  height: 50px;
  right: 30px;
  bottom: 30px;
  cursor: pointer;
}

.music-btn {
  transition: opacity .3s ease-in-out;
}

.music-btn.pause {
  opacity: .3;
}


.numb,
.oct{
  display: inline-block;
  position: relative;
  &::before,
  &::after{
    content: attr(data-title);
    position: absolute;
    top: 0;
    left: 0;
    overflow: hidden;
    background-color: #000;
    color: #fff;
  }

  &::before {
    left: 5px;
    text-shadow: 2px 0 #00ffea;
    animation: glitch-data 3s infinite linear;
  }

  &::after {
    left: 5px;
    text-shadow: -2px 0 #fe3a7f;
    animation: glitch-data 2s infinite linear;
  }
}



.glitch__img {
  background-image: url('../img/one.png');
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  width: 400px;
  height: 650px;
  animation: img-one 3s infinite linear 3s;
  img {
    filter: sepia(1) hue-rotate(310deg) saturate(8);
  }
  position: relative;

  &::before,
  &::after {
    position: absolute;
    content:'';
    width: 100%;
    // height: 100%;
    top: 0;
    // left: 0;
    right: 0;
    background-repeat: no-repeat;
    background-position:center;
    background-size: cover;
  }

  &::before {
    background-image: url('../img/two.png');
    // height: 106%;
    // top: 3px;
    // left: 10px;

    height: 110%;
    top: 0;
    left: 8px;

    z-index: 1;
    opacity: 0;
    animation: img-two  3s infinite linear 3s;
  }
  &::after {
    background-image: url('../img/three.png');
    top: 20px;
    height: 109%;
    left: 10px;
    z-index: 2;
    opacity: 0;
    animation: img-three 3s infinite linear 3s;
  }
}
