body,
html {
  height: 100%;
  background-color: var(--dark);
  text-transform: uppercase;
  color: var(--white);
}

.page {
  height: 100%;
  -webkit-text-size-adjust: 100%;
}

.page__body {
  position: relative;
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

.hidden {
  overflow: hidden;
}

.wrapper {
  display: flex;
  flex-direction: column;
  overflow: hidden;
  min-height: 100%;
}

.main {
  flex-grow: 1;
}

.container {
  max-width: 1230px;
  padding: 0 15px;
  margin: 0 auto;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.list-reset {
  list-style: none;
  padding: 0;
  margin: 0;
}

.btn-reset {
  border: none;
  padding: 0;
  background: transparent;
  cursor: pointer;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

:root {
  --dark: #000;
  --white: #F9EDED;
  --gatter: 50px;
}

.wrapper {
  min-height: 100vh;
  height: 100%;
  overflow-y: auto;
}

.main {
  margin: var(--gatter);
}

.hero {
  position: relative;
  height: 100%;
  border: 1px solid var(--white);
  display: flex;
  align-items: center;
  justify-content: space-between;
  overflow: hidden;
}

.descr-left {
  max-width: 460px;
  width: 100%;
  text-align: center;
  margin-left: 60px;
}
.descr-left__subtitle {
  font-size: 14px;
  line-height: 20px;
  letter-spacing: 11px;
}
.descr-left__text {
  position: relative;
  font-size: 70px;
  line-height: 70px;
  letter-spacing: -7px;
  text-shadow: 0.05em 0 0 #00fffc, -0.03em -0.04em 0 #fc00ff, 0.025em 0.04em 0 #fffc00;
  animation: glitch 1s infinite;
}
.descr-left__text::before, .descr-left__text::after {
  position: absolute;
  content: "halloween";
  top: 0;
  left: 0;
  left: 36px;
  letter-spacing: 1px;
  font-weight: 700;
  will-change: transform;
}
.descr-left__text::before {
  animation: glitch 0.75s infinite;
  clip-path: polygon(0 0, 100% 0, 100% 40%, 0 40%);
  transform: translate(-0.04em, -0.03em);
  transform: translate(-6px, -4px);
  opacity: 0.75;
}
.descr-left__text::after {
  animation: glitch 0.5s infinite;
  clip-path: polygon(0 60%, 100% 60%, 100% 100%, 0 100%);
  transform: translate(1px, 5px);
  opacity: 0.75;
}
.descr-left__party {
  font-size: 18px;
  line-height: 28px;
  letter-spacing: 50px;
  padding-left: 60px;
}

.descr-right {
  max-width: 600px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-right: 20px;
}
.descr-right__dj {
  font-size: 18px;
  line-height: 20px;
  text-align: center;
  max-width: 250px;
  margin-bottom: 30px;
}
.descr-right__date {
  font-size: 65px;
  margin-bottom: 30px;
}
.descr-right__date sup {
  top: -25px;
  font-size: 30px;
}
.descr-right__text {
  font-size: 18px;
  line-height: 20px;
  text-align: center;
  margin-bottom: 40px;
}

.my-site {
  position: relative;
  font-size: 40px;
}
.my-site::before, .my-site::after {
  position: absolute;
  content: "sfizer.ru";
  top: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  color: var(--white);
  background-color: var(--dark);
}
.my-site:hover::before {
  animation: glitch-site 4s linear;
}
.my-site:hover::after {
  animation: glitch-site 3s linear;
}

.music {
  position: absolute;
  width: 50px;
  height: 50px;
  right: 30px;
  bottom: 30px;
  cursor: pointer;
}

.music-btn {
  transition: opacity 0.3s ease-in-out;
}

.music-btn.pause {
  opacity: 0.3;
}

.numb,
.oct {
  display: inline-block;
  position: relative;
}
.numb::before, .numb::after,
.oct::before,
.oct::after {
  content: attr(data-title);
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  background-color: #000;
  color: #fff;
}
.numb::before,
.oct::before {
  left: 5px;
  text-shadow: 2px 0 #00ffea;
  animation: glitch-data 3s infinite linear;
}
.numb::after,
.oct::after {
  left: 5px;
  text-shadow: -2px 0 #fe3a7f;
  animation: glitch-data 2s infinite linear;
}

.glitch__img {
  background-image: url("../img/one.png");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  width: 400px;
  height: 650px;
  animation: img-one 3s infinite linear 3s;
  position: relative;
}
.glitch__img img {
  filter: sepia(1) hue-rotate(310deg) saturate(8);
}
.glitch__img::before, .glitch__img::after {
  position: absolute;
  content: "";
  width: 100%;
  top: 0;
  right: 0;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}
.glitch__img::before {
  background-image: url("../img/two.png");
  height: 110%;
  top: 0;
  left: 8px;
  z-index: 1;
  opacity: 0;
  animation: img-two 3s infinite linear 3s;
}
.glitch__img::after {
  background-image: url("../img/three.png");
  top: 20px;
  height: 109%;
  left: 10px;
  z-index: 2;
  opacity: 0;
  animation: img-three 3s infinite linear 3s;
}

