@media (max-width: 1400px) {

  .hero {
    flex-direction: column;
    padding: 25px 0 25px;
    justify-content: center;
  }

  .descr-left {
    margin-left: 20px;
  }

  .descr-left__text {
    font-size: 45px;
    line-height: 45px;
    letter-spacing: -4px;
  }

  .descr-left__text::after, .descr-left__text::before {
    left: 100px;
    letter-spacing: 2px;
    font-weight: 600;
  }

  .descr-left__party {
    font-size: 16px;
    line-height: 25px;
    letter-spacing: 28px;
    padding-left: 54px;
  }

  .descr-left__subtitle {
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 7px;
  }

  .glitch__img {
    width: 240px;
    height: 450px;
  }
  .descr-right {
    padding: 0px 15px 0;
  }

  .descr-right__dj {
    font-size: 16px;
    line-height: 18px;
    max-width: 233px;
    margin-bottom: 15px;
  }

  .descr-right__date {
    font-size: 40px;
    margin-bottom: 15px;
  }
  .descr-right__date sup {
    font-size: 12px;
    top: -20px;
  }

  .descr-right__text {
    font-size: 16px;
    line-height: 18px;
    margin-bottom: 20px;
  }


  .my-site {
    position: relative;
    font-size: 23px;
  }

  .music {
    width: 40px;
    height: 40px;
    right: 15px;
    bottom: 15px;
  }
}

