@media (max-width: 576px) {
  .main {
    margin: calc(var(--gatter)/2);
  }



  .descr-right__date {
    font-size: 30px;
    margin-bottom: 10px;
  }

  .descr-right__date sup {
    font-size: 9px;
    top: -15px;
  }

  .descr-right__dj {
    font-size: 14px;
    line-height: 16px;
    margin-bottom: 10px;
  }

  .descr-right__text {
    font-size: 14px;
    line-height: 16px;
    margin-bottom: 25px;
  }

  .descr-left__subtitle {
    display: block;
  }

  .descr-left__text {
    display: inline;
  }

  .descr-left__text::after, .descr-left__text::before {
    left: 0;
    top: 12px;
  }
}

@media (max-width: 420px) {
  .hero {
    flex-direction: column;
    padding: 25px 0;
    justify-content: space-evenly;
  }
  .glitch__img {
    width: 220px;
    height: 400px;
}

  .descr-left__text {
    font-size: 35px;
    line-height: 35px;
    letter-spacing: -1.5px;
  }

  .descr-left__text::after, .descr-left__text::before {
    letter-spacing: 4.8px;
  }

  .latter-400  {
    font-weight: 600;
  }

  .descr-left__subtitle {
    margin-bottom: 8px;
  }
  .descr-left__party {
    line-height: 25px;
    letter-spacing: 21px;
    padding-left: 21px;
    margin-top: 8px;
  }

  .descr-left__subtitle {
    font-size: 11px;
    line-height: 14px;
    letter-spacing: 5px;
  }
}
